<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
  <h5 class="text-uppercase">Remark：</h5>
  <!-- <div v-dompurify-html="newData" style="white-space: pre-line"></div> -->
  <div class="notepad mb-4">
    <div class="paper" contenteditable="true" @input="change" v-text="noteStr"></div>
  </div>
</template>

<style lang="scss" scoped>
$paper-color: #f1ede9;
$paper-line: #94acd4;

.notepad {
  width: 80%;
  max-width: 600px;
  box-shadow: 10px 10px 40px rgba(black, 0.15);
  border-radius: 0 0 10px 10px;
  overflow: hidden;
}

.paper {
  width: 100%;
  height: 100%;
  min-height: 10vh;
  padding: 4px 20px 0 20px;
  background: repeating-linear-gradient(
    $paper-color,
    $paper-color 31px,
    $paper-line 31px,
    $paper-line 32px
  );
  font-family: 'Shadows Into Light', cursive;
  line-height: 32px;
  outline: 0;
  font-size: 22px;
}
@media (max-width: 576px) {
  .notepad {
    width: 100%;
  }
}
</style>

<script>
import emitter from '@/assets/javascript/emitter';

export default {
  data() {
    return {
      noteStr: `Hello, this is a paper.
      Click to write your message.`,
      newData: `Hello, this is a paper.
      Click to write your message.`,
    };
  },
  methods: {
    change(e) {
      if (e.target.innerHTML.length < 280) {
        this.newData = e.target.innerHTML;
        emitter.emit('remark', this.newData);
      } else {
        alert("word count don't > 280");
      }
    },
  },
};
</script>
